<template>

    <tituloBusqueda titulo="Consulta de Nomina"
        descripcion="Realiza la busqueda de un maestro de acuerdo a los filtros."></tituloBusqueda>

    <div class="formularios">
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="curp" @keypress="valida_curp" @focus="limpia" class="form-control"
                        aria-describedby="id" placeholder="CURP">
                </div>
            </div>
            <div class="col">
                <input_seccion  @seccion_val="(n)=>seccion=n"></input_seccion>
            </div>
            <div class="col">
                <input_nomina  @nomina_val="(n)=>nomina=n"></input_nomina>
            </div>
        </div>
        <div class="row">
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="paterno" @keypress="valida_paterno" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="id" placeholder="APELLIDO PATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="materno" @keypress="valida_materno" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="curp" placeholder="APELLIDO MATERNO">
                </div>
            </div>
            <div class="col-md-4">
                <div class="form-group">
                    <input type="text" v-model="nombre" @keypress="valida_nombre" @keyup="mayuscula()" @focus="limpia"
                        class="form-control" aria-describedby="rfc" placeholder="NOMBRE">
                </div>
            </div>
        </div>
        <div class="buttons ">
            <a  class="btn-outline primary" @click="buscarnomina">
                Buscar
            </a>
        </div>

    </div>
    
    <table class="table table-hover tbl-resultados">
        <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">CURP</th>
                <th scope="col">MAESTRO</th>
                <th scope="col">PLAZA</th>
                <th scope="col">TIPO</th>
                <th scope="col">CONCEPTO</th>
                <th scope="col">IMPORTE</th>
                <th scope="col">SECCION</th>
                <th scope="col">QNAPAGO</th>
                <th scope="col">CONTRATO</th>
                <th scope="col">ORDEN</th>
                <th scope="col">CVE.BENEF</th>
                <th scope="col">NOMINA</th>

            </tr>
        </thead>
        <tbody>
            <tr v-for="( nomina, index) in arr_nomina" :key="{index}">
                <th scope="row">{{index+1}}</th>
                <td>{{nomina.curp}}</td>
                <td>{{nomina.nom_completo}}</td>
                <td>{{nomina.plaza}}</td>
                <td>{{nomina.tipo_cpto}}</td>
                <td>{{nomina.concepto}}</td>
                <td>{{nomina.importe}}</td>
                <td>{{nomina.seccion}}</td>
                <td>{{nomina.qna_pago}}</td>
                <td>{{nomina.contrato}}</td>
                <td>{{nomina.num_orden}}</td>
                <td>{{nomina.cve_benef}}</td>
                <td>{{nomina.nomina}}</td>
            </tr>

        </tbody>
    </table>
    <miModal titulo='NOMINA' :mensaje=mensajeModal></miModal>
    <myFooter />

</template>

<script>

import { defineAsyncComponent } from 'vue'
import axios from 'axios'

const input_seccion = defineAsyncComponent(() => import('@/modules/shared/inputs/seccion.vue'))
const input_nomina = defineAsyncComponent(() => import('@/modules/shared/inputs/nomina.vue'))
const myFooter = defineAsyncComponent(() => import('@/modules/shared/components/Footer.vue'))
const tituloBusqueda = defineAsyncComponent(() => import('@/modules/shared/components/TituloBusqueda.vue'))
const miModal = defineAsyncComponent(() => import('@/modules/shared/components/ModalAceptar.vue'))


//Variables de captura
let curp = ''
let paterno = ''
let materno = ''
let nombre = ''
let seccion = ''
let nomina = ''
let plaza
//Variables de aplicativo
let arr_nomina = []
//Variable del modal
let mensajeModal = ''


export default ({

    components: {
        input_seccion,
        input_nomina,
        myFooter,
        tituloBusqueda,
        miModal
    },

    data() {
        return {
            curp, paterno, materno, nombre, seccion, nomina, plaza,
            mensajeModal,
            arr_nomina
        }
    },
    methods: {
        async buscarnomina() {

            /*let params = {

                seccion: this.seccion,
                curp: this.curp,
                ap_pat: this.paterno,
                ap_mat: this.materno,
                nombre: this.nombre,
                nomina: this.nomina
            }*/
            console.log("Buscando..")
            try {
                const response = await this.obtenerNomina()
                const { estatus, errorText, num_registros, resultado } = response

                console.log(response)

                if (estatus === 'error') {
                    this.mensajeModal = errorText
                    $('#staticBackdrop2').modal('show')
                }

                if (estatus === 'ok') {
                    if (num_registros < 1) {
                        this.mensajeModal = 'No se encontro información'
                        $('#staticBackdrop2').modal('show')
                    }
                    else {
                        this.arr_nomina = response.resultado
                        console.log("RESULTADO::", resultado)
                    }
                }

            } catch (error) {
                console.log(error)
                this.mensajeModal = error.data.errorText
                $('#staticBackdrop2').modal('show')
            }


        },
        async obtenerNomina() {
            try {
                //var json = JSON.stringify(params);

               // console.log(json)
                const response = await axios.get(`http://localhost:8080/api/nominapago`, {
                    params: {
                        seccion:this.seccion,
                        curp:   this.curp,
                        ap_pat: this.paterno,
                        ap_mat: this.materno,
                        nombre: this.nombre,
                        nomina: this.nomina,
                        plaza:  this.plaza
                    }
                })
                return response.data
            }
            catch (error) {
                console.log(error)
                return error.response.data
            }

        },
        valida_curp(e) {
            if (this.curp.length >= 18)
                e.preventDefault()
        },
        valida_paterno(e) {
            if (this.paterno.length >= 25)
                e.preventDefault()
        },
        valida_materno(e) {
            if (this.materno.length >= 25)
                e.preventDefault()
        },
        valida_nombre(e) {
            if (this.nombre.length >= 25)
                e.preventDefault()
        },
        mayuscula() {
            this.nombre = this.nombre.toUpperCase()
            this.paterno = this.paterno.toUpperCase()
            this.materno = this.materno.toUpperCase()
        },


    }

})

</script>


<style scoped>
    table {
        margin: 35px;
        width: 835px;
    }
    
    thead {
        background-color: var(--color-primary-2);
        color: var(--color-white);
       
        padding: 5px;
        border-radius: 5px;
    }
    
    tbody {
        min-height: 200px;
        max-height: 450px;
        overflow-y: scroll;
    
        padding-left: 5px;
        overflow-x: hidden;
        font-size: smaller;
    }
    
    tbody tr {
        border-bottom: var(--color-grey-2) solid 1px;
        padding: 5px;
        background-color: var(--color-white);
        border-radius: 5px;
    }
    
    
    .tbl-resultados {
        font-size: var(--font-size);
        margin-top: 20px;
        text-align: center;
        /*overflow: scroll;*/
    }
    .tbl-resultados .tbl-header {
        background-color: var(--color-primary-2);
        color: var(--color-white);
        display: flex;
        height: 40px;
        padding: 5px;
        border-radius: 5px;
        /*border: var(--color-primary-3) solid 1px;*/
    }
    
    
    .tbl-header-data {
        margin: 0px 5px;
        padding: 2px;
        width: 120px;
    }
    
    .tbl-resultados .tbl-data {
        border-bottom: var(--color-grey-2) solid 1px;
        padding: 5px;
        background-color: var(--color-white);
        border-radius: 5px;
    }
    
    .tbl-data:nth-of-type(odd) {
        opacity: 70%;
    }
    
    .tbl-data:nth-of-type(even) {
        opacity: 100%;
    }
    
    .tbl-maestros {
        min-height: 200px;
        max-height: 450px;
        overflow-y: scroll;
    
        padding-left: 5px;
        overflow-x: hidden;
        font-size: smaller;
    }
    
    .tbl-dato {
        width: 120px;
        margin: 0px 5px;
        padding: 2px;
        text-align: center;
    }
    
    .tbl_short {
        width: 40px;
    }
    
    .tbl_short-s {
        width: 10px;
    }
    
    .tbl_long {
        width: 150px;
    }

    td{
        vertical-align: middle;
        padding-inline-start: initial;
    }

    td input{
        font-size: small;
    }

    .input_disables{
        border: 1px;;
    }

    td input:disabled {
        border:none;
        background: none;
    }

    .img{
        transition: 0.3s;
        padding: 2px;
        border-radius: 6px;
        cursor:pointer
    }

    .img:hover {
        opacity:10;
        background-color:whitesmoke ;
    }

    
    </style>